@media only screen and (min-width: 320px) {
  .container {
    height: 90vh;
    width: 100%;
    object-fit: cover;
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
  }
  .container h1 {
    font-size: 1.45rem;
    word-spacing: 0.25em;
    font-family: Arbutus, Rye, Henny Penny, Luckiest Guy, McLaren, Bowlby One SC,
      Finger Paint, Fredoka One, Sigmar One, Chewy, Nova Cut, Miltonian,
      Irish Grover, Black Ops One, Diplomata SC, Pirata One, Ribeye, New Rocker,
      Bubblegum Sans, Georgia, serif;
    z-index: 2;
    position: absolute;
    background: radial-gradient(red, var(--yellow-primary));
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    width: 98%;
    max-width: 750px;
    text-align: center;
  }
  .container h1 span {
    -webkit-background-clip: initial;
    -webkit-text-fill-color: initial;
  }
  .container h1 span:nth-child(even) {
    color: var(--white-primary);
    background-color: var(--gray-primary);
    font-family: Henny Penny;
  }
  .container h1 span:nth-child(odd) {
    background-color: white;
    color: black;
  }
  .container img {
    width: 100%;
    height: 100%;
    max-width: 800px;
  }
}
@media only screen and (min-width: 359px) {
  .container h1 {
    font-size: 1.65rem;
  }
}
@media only screen and (min-width: 374px) {
  .container h1 {
    font-size: 1.7rem;
  }
}
@media only screen and (min-width: 410px) {
  .container h1 {
    font-size: 1.8rem;
  }
}
@media only screen and (min-width: 413px) {
  .container h1 {
    font-size: 1.9rem;
    text-align: center;
  }
}
@media only screen and (min-width: 768px) {
  .container h1 {
    font-size: 2.2rem;
    width: 95%;
  }
}
