* {
  box-sizing: border-box;
  padding: 0;
  margin: 0;
  font-family: "Lato", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
:root {
  --orange-primary: #e7473c;
  --orange-secondary: #f08f89;
  --yellow-primary: #fecc06;
  --yellow-secondary: #e2a823;
  --white-primary: #f0f0f0;
  --gray-primary: #3c3b3b;
  --green-dark: #424731;
  --green-light: #6d903c;
}

button {
  transition: all linear 300ms;
}
button:hover {
  cursor: pointer;
}

#container {
  position: relative;
  background-color: #e7473c;
  background-color: var(--orange-primary);
}

